<template>
  <div class="personal_verify">
    <listSearch 
      v-model="searchFormData"
      @searchConfirm="goSearch"
      :searchArr="searchArr"></listSearch>
    <div class="content">
      
      <div class="table_box mgt20">
        <Table ref="selectionTable" 
          :columns="columns"
          :data="bodyList">
          <template slot-scope="{ row }" slot="name">
            <span style="cursor:pointer" @click="goDetail(row)">{{ row.name }}</span>
          </template>
          <template slot-scope="{ row }" slot="action">
            <div>
              <Button type="primary" @click="btnOperate('accept', row)">同意</Button>
              <Button type="primary" @click="btnOperate('reject', row)">驳回</Button>
            </div>
          </template>
        </Table>
        <!-- 分页 -->
        <Page class="mgt20" :total="pageObj.total" 
          show-elevator
          show-sizer 
          @on-page-size-change="pageSizeChange"
          @on-change="pageNumChange"/>
      </div>
    </div>
    <!-- 弹窗-->
    <!-- <popupBox :show="popupShow" 
      @popupConfirm="operate" 
      @popupCancel="popupCancel"
      >
      <div class="popup_content">确定{{popupText}}?</div>
    </popupBox> -->
  </div>
</template>

<script>
import listSearch from '@/components/list-search/search.vue'
import popupBox from '@/components/popup/popup-box'
import mixin from './mixin'
import { mapActions } from "vuex";

export default {
  name: 'personal_verify',
  components: { listSearch, popupBox },
  mixins: [mixin],
  data () {
    return {
      userType: 1, // 1:个人 2:企业
      state: 3, // 状态 貌似3是带审核 具体需要字典表
      // 驳回操作参数
      operateObj: {
        userIds: null,
        action: null,
        reason: '1'
      },
      popupShow: false,
      popupText: '',
      popupContent: '',
      searchFormData: {
        personalName: null,
        companyName: null,
        checkbox: [],
        radio: null,
        status: null
      },
      pageObj: {
        pageSize: 10,
        pageIndex: 0,
        total: null,
      },
      searchArr: [
        {type: 'input', value: 'personalName', label: '个人姓名', placeholder: '请输入个人姓名'},
        {type: 'input', value: 'companyName', label: '企业名称', placeholder: '请输入企业名称'},
        {type: 'select', value: 'status', label: '状态',
         arr: [
          {label: '通过', value: 1},
          {label: '未通过', value: 2}
         ], 
          placeholder: '请选择'},
      ],
      columns: [
        {
          title: '姓名',
          slot: 'name'
        },
        {
          title: '证件类型',
          key: 'idcardTypeDesc'
        },
        {
          title: '证件号码',
          key: 'idcard'
        },
        {
          title: '性别',
          key: 'sexDesc'
        },
        {
          title: '联系地址',
          key: 'address'
        },
        {
          title: '手机号码',
          key: 'phone'
        },
        {
          title: '状态',
          key: 'stateDesc'
        },
        {
          title: '操作',
          slot: 'action'
        }
      ],
      bodyList: []
    }
  },
  watch: {
    // searchFormData (newVal) {
    //   console.log(newVal)
    // }
  },
  mounted () {
    this.getList()
  },
  methods: {
    // 搜索
    goSearch () {
      console.log(this.searchFormData)
    },
    // 翻页
    pageNumChange (val) {
      console.log(val)
      this.pageObj.pageIndex = val
    },
    // 修改pageSize
    pageSizeChange (val) {
      console.log(val)
      this.pageObj.pageSize = val
    },
    // 跳转详情页
    goDetail (data) {
      this.$router.push({
        path: 'verify_detail/',
        query: {
          id: data.id,
          type: this.userType
        }
      })
    },
  }
}
</script>
<style lang="less">
@import url('../../style/iview_table.less');
</style>
<style lang="less" scoped>
.personal_verify {
  .content {
    margin-top: 30px;
    padding: 20px 10px;
    background: #fff;
    border-radius:4px;
    .buttons {
      padding-right: 20px;
      text-align: right;
    }
    .table_box {
      // padding: 0 2px;
    }
  }
  .mgt20 {
    margin-top: 20px;
  }
  .mgl10 {
    margin-left: 10px;
  }
}
.popup_content {
  text-align: center;
  font-size:18px;
  font-family:PingFangSC-Semibold,PingFang SC;
  font-weight:600;  
  color: #3B4453;
}
// /deep .ivu-modal-confirm-body {
//   margin-left: 0;
// }
</style>